import React from 'react'
import {BrowserRouter as Router,Route,NavLink,Switch,withRouter} from 'react-router-dom'
import 'antd-mobile/dist/antd-mobile.css'
import {Button,TabBar} from 'antd-mobile'

class Tab extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'blueTab',
    };
  }
  

  render() {
    const {pathname} = this.props.location;
    return (
      <div style={{ position: 'fixed', height: '100%', width: '100%', top: 0 }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#33A3F4"
          barTintColor="white"
          hidden={this.state.hidden}
        >
          <TabBar.Item
            title="首页"
            key="home"
            icon={
              <i className='iconfont icon-hengxing'></i>
            }
            selectedIcon={
            <i className='iconfont icon-hengxing'></i>
            }
            selected={pathname === '/Home'}
            badge={1}
            onPress={() => {
              this.props.history.push('/Home')
              // this.setState({
              //   selectedTab: 'blueTab',
              // });
            }}
            data-seed="logId"
          >
            
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i className='iconfont icon-leida'></i>
            }
            selectedIcon={
              <i className='iconfont icon-leida'></i>
            }
            title="商品"
            key="goods"
            badge={'new'}
            selected={pathname === '/Goods'}
            onPress={() => {
              this.props.history.push('/Goods')
          }}
          data-seed="logId1"
        >
        
        </TabBar.Item>
        <TabBar.Item
          icon={
            <i className='iconfont icon-yuzhoufeichuan'></i>
          }
          selectedIcon={
            <i className='iconfont icon-yuzhoufeichuan'></i>
          }
          title="购物车"
          key="shopping"
          dot
          selected={pathname === '/Shopping'}
          onPress={() => {
            this.props.history.push('/Shopping')
            // this.setState({
            //   selectedTab: 'greenTab',
            // });
          }}
        >
          
        </TabBar.Item>
        <TabBar.Item
            icon={
              <i className='iconfont icon-yuhangyuan'></i>
            }
            selectedIcon={
              <i className='iconfont icon-yuhangyuan'></i>
            }
            title="我的"
            key="my"
            selected={pathname === '/My'}
            onPress={() => {
              this.props.history.push('/My')
            }}
          >
            
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}

export default withRouter(Tab);
